/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */
.wp-guide-manual {
  border: 1px dotted red; }

.components-guide__page {
  padding: 20px; }
  .components-guide__page, .components-guide__page * {
    line-height: 1.5; }
  .components-guide__page img {
    height: auto; }
  .components-guide__page p {
    margin: 1em 0; }
  .components-guide__page p, .components-guide__page li {
    font-size: 13px; }
  .components-guide__page ul {
    list-style: circle;
    padding-left: 40px; }

.components-guide .components-modal__header .components-button:hover {
  background: var(--wp-admin-theme-color); }

@media (min-width: 600px) {
  .components-guide {
    width: 1000px;
    font-size: 25px; } }

.guide_ordering.guide_ordering {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 20px;
  gap: 20px; }
  @media (max-width: 1024px) {
    .guide_ordering.guide_ordering {
      grid-template-columns: repeat(2, 1fr); } }
  @media (max-width: 600px) {
    .guide_ordering.guide_ordering {
      grid-template-columns: repeat(1, 1fr);
      grid-gap: 10px;
      gap: 10px; } }
  .guide_ordering.guide_ordering .sortable-guides li {
    cursor: move; }
    .guide_ordering.guide_ordering .sortable-guides li:hover {
      opacity: 0.6; }
  .guide_ordering.guide_ordering .screen {
    background: white;
    width: auto; }
    .guide_ordering.guide_ordering .screen .screen-inner-wrapper {
      background: transparent; }
